<!--  -->
<template>
  <div class="MineInformation">
      <div class="MineInfo_title">
        <img @click="InfoReturn" :src="staticImgH+'zuojiantou.png'" alt="">
        <span></span>
        <span class="submit" @click="submit"></span>
      </div>
      <ul class="MineInfo_List">
        <li>
          <span></span>
          <div class="imgInput">
              <img :src="headPicM" alt="">
              <input type="file" class="upload" @change="uploadFile" ref="inputer" accept="image/*"/>
          </div>
          
        </li>
        <li>
          <span></span>
          <div class="MineInfo_ListRight" @click="changeName">
            <span>{{nickNameM}}</span><img :src="staticImgH+'MineRight.png'" alt="">
          </div>
        </li>
        <li>
          <span></span>
          <div @click="SignatureReturn" class="MineInfo_ListRight">
            <span>{{signatureM}}</span><img :src="staticImgH+'MineRight.png'" alt="">
          </div>
        </li>
        <li>
          <span></span>
          <div @click="secC"  class="MineInfo_ListRight">
            <span v-if='sexM==1'></span><span v-if='sexM==2'></span><img :src="staticImgH+'MineRight.png'" alt="">
          </div>
        </li>
        <li>
          <span></span>
          <div  class="MineInfo_ListRight">
            <span>{{ageM}}</span><img :src="staticImgH+'MineRight.png'" alt="">
          </div>
        </li>
        <li>
          <span></span>
          <div  class="MineInfo_ListRight">
            <span>{{heightM}}cm</span><img :src="staticImgH+'MineRight.png'" alt="">
          </div>
        </li>
        <li>
          <span></span>
          <div  class="MineInfo_ListRight">
            <span>{{weightM}}kg</span><img :src="staticImgH+'MineRight.png'" alt="">
          </div>
        </li>
        <li>
          <span></span>
          <div @click="tocity"  class="MineInfo_ListRight">
            <span>{{cityM}}</span><img :src="staticImgH+'MineRight.png'" alt="">
          </div>
        </li>
        <li>
          <span></span>
          <div class="MineInfo_ListRight">
            <span>{{constellationM}}</span><img :src="staticImgH+'MineRight.png'" alt="">
          </div>
        </li>
      </ul>
      <!--  -->
      <div class="sex_wrap" @click="showSexC"  v-if="sexShow">
             <div class="sex">
               <span @click.stop="sexSelC(item.id)" v-for="(item,index) in sexData" :key="index">{{item.name}}</span>
             </div>
      </div>
      <!--  -->
         <transition name="fade">
            <div class="promptFather" v-if="showPrompt">
                <div class="prompt" >
                    {{promptContent}}
                </div>
            </div>
        </transition>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import {mapMutations} from 'vuex'
import qs from 'qs'
export default {
  name:'MineInformation',
  data () {
    return {
      sexData:[
        {
          name:'',
          id:1,
        },
        {
          name:'',
          id:2
        }
      ],
      sexShow:false, //
      personData:'',//
      headPicM:'',//
      nickNameM:'',//
      signatureM:"",//
      sexM:'',//
      ageM:"",//
      heightM:'',//
      weightM:'',//
      cityM:'',//
      constellationM:'',//
      blooM:'',//
      nationM:'',//
      cultureM:'',//
      usernameM:'',//
      userIntroductionM:'',//


      // 
        promptContent:'', //
        showPrompt:false,//
        timer2:'',//

      // 
      formData:new FormData(),
      imgLen:0,
      fil:'',
    };
  },
  // components: {},
   computed:{
        ...mapState(['staticImgH','tokenH','nickNamePerX','SignaturePerX','cityNamePerX','tokenH'])
    },
  mounted(){
        this.getInformation()
   },
  methods: {
    // 
    getInformation(){
       var obj=qs.stringify({
        
      })
      this.$http.post('api/player/per_sonals',obj,{
            headers: {
                'authorization': this.tokenH
            }
      }).then((res)=>{
          if(res.data.code==200){
              var personData=res.data.data
              this.headPicM=personData.head_pic
              if(this.nickNamePerX){
                  this.nickNameM=this.nickNamePerX;
              }else{
                this.nickNameM=personData.username
              }
              if(this.SignaturePerX){
                this.signatureM=this.SignaturePerX
              }else{
                this.signatureM=personData.signature
              }
              this.sexM=personData.sex
              this.ageM=personData.age
              this.heightM=personData.height
              this.weightM=personData.weight
              if(this.cityNamePerX){
                this.cityM=this.cityNamePerX
              }else{
                  this.cityM=personData.city
              }
              
              this.constellationM=personData.constellation
          }else{
            var self=this
              clearInterval(self.timer2);
                  this.promptContent=res.data.msg
                  this.showPrompt=true
                  self.timer2=setTimeout(function(){
                      self.showPrompt=false
                      clearInterval(self.timer2);
                  },2000)
              return false;
          }
      })
    },
    // 
     uploadFile(){
        let inputDOM = this.$refs.inputer;
        // DOM
        this.fil = inputDOM.files;
        let len=this.fil.length;
        if(len>1){
          alert('1');
          return false;
        }
        for (let i=0; i < this.fil.length; i++) {
          let size = Math.floor(this.fil[i].size / 1024);
          if (size > 5*1024*1024) {
            alert('5M');
            return false
          }
           this.formData.append('avatar',this.fil[i])
        }
        this.$http.post('api/user/avatar', this.formData,{
            headers: {
                    'authorization': this.tokenH
                }
        }).then(res => {
              this.getInformation()
        });
      },
    // 
    changeName(){
        this.nickNamePerXs(this.nickNameM)
        this.$router.push('/changeName')
    },
    // 
    InfoReturn(){
        this.$router.push('/Mine')
    },
    // 
    SignatureReturn(){
      this.SignaturePerXs(this.signatureM)
      this.$router.push('/Signature')
    },
    // 
    secC(){
      this.sexShow=true;
    },
    // 
    sexSelC(id){
        this.sexM=id
        this.sexShow=false;
    },
    // 
    showSexC(){
      this.sexShow=false;
    },
    // 
    tocity(){
        this.$router.push('/City')
    },
    // 
    submit(){
       var obj=qs.stringify({
          signature:this.signatureM,
          constellation:this.constellationM,
          bloo:this.blooM,
          nation:this.nationM,
          culture:this.cultureM,
          sex:this.sexM,
          age:this.ageM,
          city:this.cityM,
          weight:this.weightM,
          head_pic:this.headPicM,
          username:this.usernameM,
          user_introduction:this.userIntroductionM
       })
       this.$http.post('api/player/add_sonals',obj,{
          headers: {
              'authorization': this.tokenH
          }
        }).then((res)=>{
            if(res.data.code==200){
                var self=this
                clearInterval(self.timer2);
                    this.promptContent=''
                    this.showPrompt=true
                    self.timer2=setTimeout(function(){
                        self.showPrompt=false
                        clearInterval(self.timer2);
                    },2000)
              return false;
            }else{
              var self=this
              clearInterval(self.timer2);
                  this.promptContent=res.data.msg
                  this.showPrompt=true
                  self.timer2=setTimeout(function(){
                      self.showPrompt=false
                      clearInterval(self.timer2);
                  },2000)
              return false;
            }
          })
    },
     ...mapMutations(['specialInfos','nickNamePerXs','cityNamePerXs','SignaturePerXs']),
    
  }
}

</script>
<style scoped lang="stylus">
.MineInfo_title{
  width:100%;
  height:1.23rem;
  display :flex;
  align-items :center;
  justify-content :space-between;
  padding:0.4rem;
  >img{
    width:0.32rem;
    height:0.56rem;
  }
  >span{
      font-size:0.48rem;
      color:rgba(0, 0, 0, 1);
  }
  >.submit{
    font-size:0.427rem;
    color:rgba(0, 0, 0, 1);
  }
}
.MineInfo_List{
  margin:0.4rem 0.27rem;
  border-radius:0.133rem;
  box-shadow :0 0.05rem 0.3rem rgba(241, 241, 241, 0.9);
  >li{
    display :flex;
    align-items :center;
    justify-content :space-between;
    margin:0 0.32rem;
    padding:0.4rem 0;
    font-size:0.347rem;
    border-bottom:0.03rem solid rgba(241, 241, 241, 1);
    >.imgInput{
        width:1.07rem;
        height:1.07rem;
        position :relative;
        >img{
            width:1.07rem;
            height:1.07rem;
            border-radius:50%;
            background :pink;
        }
        >input{
          width:1.07rem;
            height:1.07rem;
            border-radius:50%;
            position :absolute;
            top:0;
            left:0;
            opacity :0;
            overflow :hidden;
            
        }

    }
    
    >.MineInfo_ListRight{
      color:rgba(51, 51, 51, 0.6);
      font-size:0.32rem;
      display :flex;
      align-items :center;
      >span{
        flex:1;
        overflow :hidden;
        text-overflow :ellipsis;
        white-space:nowrap;
      }
      >img{
        width:0.187;
        height:0.293rem;
        margin-left:0.32rem;
      }
    }
  }
}
.sex_wrap{
  width:100%;
  height:100%;
  background :rgba(0,0,0,0.9);
  position :fixed;
  top:0;
  left:0;
  >.sex{
    width:8rem;
    height:2.83rem;
    display :flex;
    flex-direction :column;
    background :#fff;
    border-radius:0.133rem;
    position :absolute;
    top:0;
    bottom:0;
    left:0;
    right:0;
    margin:auto;
    >span{
      width:100%;
      height:1.413rem;
      line-height :1.413rem;
        border-bottom:0.03rem solid rgba(204, 204, 204, 1);
        font-size:0.373rem;
        color:rgba(0, 0, 0, 1);
        padding:0 0.4rem;
        &:last-child{
          border-bottom:0;
        }
    }
  }
}
    // 
.promptFather{
    width:100%;
    position :fixed;
    left:0;
    right:0;
    top:0;
    bottom:0;
    margin:auto;
    display :flex;
    justify-content :center;
    align-items :center;
    z-index:999;
    >.prompt{
        padding:0.2rem 0.35rem;;
        background :rgba(0,0,0,0.7);
        color:#fff;
        border-radius:0.5rem;
        font-size:0.37rem;
    }
}
.fade-enter-active, .fade-leave-active {
//   transition: opacity .5s;
    transition: all .3s ease;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
   transform: translateY(0.32rem);
  opacity: 0;
}
</style>


