<!--  -->
<template>
  <div >
        <!-- header -->
            <div class="PlayerRanking_header">
                <img @click.stop="toReturn" :src="staticImgH+'zuojiantouWhite.png'" alt="">
                <span></span>
            </div>
        <!--  -->
            <div class="small_Carousel" v-if="smallSwiper.length>=1">
                        <!-- swiper v-if="comment.length>=1" swiper -->
                <swiper v-if="smallSwiper.length>=1" class="small_swiperWrap" :options="swiperOption">
                    <swiper-slide class="swiperSlide" v-for="(item,index) in smallSwiper" :key="index"> <img :src="item.head_pic"><span>{{item.nickname}}</span>{{item.amount}}</swiper-slide>
                </swiper>
            </div>
        <!--  -->
            <div class="playerBigImg">
                <img :src="detailData.head_pic" alt="">
            </div>
        <!--  -->
            <div class="player_des">
                    <div class="player_des_top">
                        <img class="player_desImg" :src="detailData.head_pic" alt="">
                        <ul>
                            <li ><img :src="staticImgH+'playBian.png'" alt=""><span class="username">{{detailData.username}}</span> </li>
                            <li> <span class="playerId">{{detailData.player_id}}</span></li>
                        </ul>
                    </div>
                    <ul class="piao_list">
                        <li>
                            <span class="piao_list_top">{{detailData.votes}}</span><span class="piao_list_bottom"></span>
                        </li>
                        <li>
                            <span class="piao_list_top">{{detailData.division_ranking}}</span><span class="piao_list_bottom"></span>
                        </li>
                        <span></span>
                </ul>
                <div class="palylike">{{detailData.signature}} </div>
                <!-- <div class="player_desName"> {{}} <span>{{detailData.names}}</span>  </div> -->
                <div class="playNum" v-if="detailData.user_id">+{{detailData.user_id}}</div>
                <ul class="player_listdes">
                        <li>
                        <img v-if="personData.sex==2" :src="staticImgH+'gril.png'" alt="">
                            <img v-if="personData.sex==1" :src="staticImgH+'boy.png'" alt="">
                        <span v-if="detailData.sex==2"></span>
                        <span v-if="detailData.sex==1"></span>
                        </li>
                    <li> <span v-if="detailData.age!==0">{{detailData.age}}</span></li>
                    <li v-if="detailData.city">{{detailData.city}}</li>
                    <li v-if="detailData.constellation">{{detailData.constellation}}</li>
                    <li v-if="detailData.height">{{detailData.height}}cm</li>
                    <li v-if="detailData.weight">{{detailData.weight}}kg</li>
                    <li><span v-if="detailData.union_id"></span><span v-else></span></li>
                </ul>
                <div class="player_btnList">
                    <span class="voteBtn" @click.stop="vote"></span>
                    <span class="attentionBtn">+</span>
                    <span class="shareBtn">Ta</span>
                    <span class="EnterBtn"></span>
                </div>
            </div>
      <div v-if="detailData.user_introduction">

        <video id="video1" width="399" height="300" poster="video_bg.jpg">
            <source :src="detailData.user_introduction" type="video/mp4" />
        </video>
      </div>
      <!--  -->
      <ul class="playerImg_list">
          <li @click="changeBigImg(index)" v-for="(item,index) in detailData.photo_introduction" :key="index">
              <img :src="item.src" alt="">
          </li>
      </ul>
      <!--  -->
      <ul class="BigplayerImg_list" v-if="showBigPlayImg" @click="hideBigPlayImg">
          <li  v-for="(item,index) in detailData.photo_introduction" :key="index" v-if="index==bigPlayImgIndex">
              <img :src="item.src" alt="">
          </li>
      </ul>
      <!--  -->
      <div class="vote_wrap" v-if="voteShow" @click.stop="hideVote">
      <div class="vote"  @click.stop>
          
          <div class="FreeTicket">
              <span class="FreeTicket_title">({{personData.user_votes}})</span>
              <span class="FreeTicket_btn" @click.stop="freeticket"></span>
          </div>
          <div class="AssistTicket">
                <div class="AssistTicket_title"></div>  
                <span class="AssistTicket_text"></span>
                <div class="SpecialTopicBody_bar">
                    <!--  -->
                    <ul>
                        <li :class="barcolorIndex==index?'SpecialTopicBody_barColor':''" @click.stop="getShopDes(item.goods_id,index)" v-for="(item,index) in shopList" :key="index">
                            <img :src="item.original_img" alt="">
                             <span @click.stop="toshopdetail(item.goods_id)">>></span>
                        </li>
                    </ul>
                </div>
                <div class="AssistTicket_btn" @click.stop="AssistTicket"></div>
          </div>
      </div>
      </div>
      <!--  -->
        <transition name="fade">
            <div class="promptFather" v-if="showPrompt">
                <div class="prompt" >
                    {{promptContent}}
                </div>
            </div>
        </transition>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import {mapMutations} from 'vuex'
import wx from 'weixin-jsapi'
import qs from 'qs'
export default {
    name:"PlayerDetails",
  data () {
    return {
        // 
        swiperOption: {//swiper3
            direction: 'vertical',
            observer:true,
            observeParents:true,
            loop:true,
            
            autoplay: {
            disableOnInteraction: false,
        },
            speed:300,
        },

        voteShow:false,   //
        detailData:'',   //
        shopList:'',  // 
        personData:'',  //
        smallSwiper:'',//
        
        test:'',  //
        dataResult:'',  //
        orderSn:'',//

        goodId:'',  //id
        barcolorIndex:-1,  //
        bigPlayImgIndex:-1,//-1
        showBigPlayImg:false,//

        // 
        promptContent:'', //
        showPrompt:false,//
        timer2:'',
    };
  },

//   components: {},

   computed:{
            ...mapState(['staticImgH','playerId','addressIdIsSel','addressId','PlayerDetailPage','playDetailVoteDiv','tokenH','playDetailShopDES','apiH','shopDetatilshow','barcolorIndexShop','shopgoodId'])
        },
  mounted(){
    //  
        var shopUrl = window.location.href
            //var shopUrl = 'http://mobile.aibebi.cn/aibei/shopList.html?goods_id=1482'
            //id
            var shopCan, value;  // 
            shopCan = shopUrl.indexOf("#")  //
            shopUrl=shopUrl.substr(shopCan+1)
            shopCan=shopUrl.indexOf('?')
            if(shopCan > 0){
                shopUrl = shopUrl.substr(shopCan + 1)  //
                var shopArr = shopUrl.split('&') // 
                var shopUrlId = {};//  goodId
                for(var i = 0; i < shopArr.length; i++) {//shopArr
                    shopCan = shopArr[i].indexOf("=");  //=
                    if(shopCan > 0){ //=
                        value = shopArr[i].substring(shopCan + 1); //=     =>value
                        shopCan = shopArr[i].substring(0, shopCan);//=  =key
                        shopUrlId[shopCan] = value;  // key valueshopUrlId
                    }
                }
                this.playerIds(shopUrlId.player_id)
        }
    //   
        if(this.playDetailVoteDiv=='true'){   //  playDetailVoteDiv 
                this.voteShow=true    //
                if(this.addressIdIsSel=='true'){   //addressIdIsSel      ture
                    this.goodId=this.playDetailShopDES.goodsId   
                    this.playerIds(this.playDetailShopDES.playerId) 
                    this.barcolorIndex=this.playDetailShopDES.barcolorIndex   //
                    this.AssistTicketTwo()
                }else if(this.shopDetatilshow=='true'){
                    this.goodId=this.shopgoodId
                    this.barcolorIndex=this.barcolorIndexShop
                }
            }else if(this.playDetailVoteDiv=='false'){
                this.voteShow=false  //
        }
    //   
        var obj=qs.stringify({
                player_id:this.playerId  
            })
            this.$http.post('/api/player/info',obj,{
                headers: {
                    'authorization':this.tokenH
                }
            }).then((res)=>{
                if(res.data.code==200){
                    console.log(res)
                    this.detailData=res.data.data
                }else{
                    var self=this
                    clearInterval(self.timer2);
                    this.promptContent=res.data.msg
                    this.showPrompt=true
                    self.timer2=setTimeout(function(){
                        self.showPrompt=false
                        clearInterval(self.timer2);
                    },2000)
                    return false;
                }
        })
    //   
        this.$http.post('/api/goods/goods_list',obj,{
            headers: {
                    'authorization': this.tokenH
                }
            }).then((res)=>{
                if(res.data.code==200){
                    this.shopList=res.data.data.result
                }else{
                    var self=this
                    clearInterval(self.timer2);
                    this.promptContent=res.data.msg
                    this.showPrompt=true
                    self.timer2=setTimeout(function(){
                        self.showPrompt=false
                        clearInterval(self.timer2);
                    },2000)
                return false;
                }
        })
    //    
        this.getpaioNUm()
    //   
        var Wobj=qs.stringify({
            player_id:this.playerId,
            type:2,
        })
        this.$http.post('/api/wechat/get_sign',Wobj,{
            headers: {
                    'authorization': this.tokenH
                }
            }).then((res)=>{
            if(res.data.code==200){
                var data=res.data.data
                this.test=data.test
                    wx.config({
                        debug: false, // ,apialertpclogpc
                        appId: data.appId, // 
                        timestamp:data.timestamp, // 
                        nonceStr: data.nonceStr, // 
                        signature: data.signature,// 
                        jsApiList: ['onMenuShareAppMessage','onMenuShareTimeline'] // JS
                    });
                    this.toFriend()
                    this.toFriendQuan()
            }else{
                    var self=this
                    clearInterval(self.timer2);
                    this.promptContent=res.data.msg
                    this.showPrompt=true
                    self.timer2=setTimeout(function(){
                        self.showPrompt=false
                        clearInterval(self.timer2);
                    },1000)
                    return false;
            }
        })
    //  
        this.$http.post('/api/first/vote_list',obj,{
            headers: {
                    'authorization': this.tokenH
                }
        }).then((res)=>{
            this.smallSwiper=res.data.data
        })
  },

  methods: {
    //   id
    toshopdetail(goodid){
        this.addressIdIsSels('false') //
        this.shopDetailReturns('/PlayerDetails')//
        this.playDetailVoteDivs('true')  //true 
        this.barcolorIndexShops(this.barcolorIndex) // 
        this.shopDetatilshows('true')  // 
        this.shopgoodIds(goodid)  //gooid
        this.userIdHs(this.detailData.user_id) //id
        this.playerIds(this.playerId)
        this.$router.push('/shopDetail')  
    },
    //   
    hideBigPlayImg(){
        this.showBigPlayImg=false
    },
    //   
    changeBigImg(index){
        this.showBigPlayImg=true
        this.bigPlayImgIndex=index
    },
       //   
      toFriend(){
                var vm=this
                var realLocation=vm.apiH+'/#/PlayerDetails?player_id='+vm.playerId 
                wx.ready(function () {   //
                    wx.onMenuShareAppMessage({ 
                        title:vm.test, // 
                        desc:'', // 
                        link:vm.apiH+'/static/html/redirect.html?app3Redirect='+encodeURIComponent(realLocation), // JS
                        imgUrl: vm.detailData.head_pic, // 
                        success: function (res) {
                        }
                    })
                });
      },
    //   
      toFriendQuan(){
          var vm=this
          var realLocation=vm.apiH+'/#/PlayerDetails?player_id='+vm.playerId 
           wx.ready(function () {   //
                wx.onMenuShareTimeline({
                        title:vm.test, // 
                        link: vm.apiH+'/static/html/redirect.html?app3Redirect='+encodeURIComponent(realLocation),  // JS
                        imgUrl:vm.detailData.head_pic, // 
                        success: function (res) {
                        },
                })
          });
      },
    //   
      getpaioNUm(){
          var obj=qs.stringify({
            })
            this.$http.post('api/user/info',obj,{
                headers: {
                    'authorization': this.tokenH
                }
            }).then((res)=>{
                if(res.data.code==200){
                    this.personData=res.data.data
                }else{
                    var self=this
                    clearInterval(self.timer2);
                            this.promptContent=res.data.msg
                            this.showPrompt=true
                            self.timer2=setTimeout(function(){
                                self.showPrompt=false
                                clearInterval(self.timer2);
                            },2000)
                        return false;
                }
            
            })
      },
        //   
        hideVote(){
            this.voteShow=false
            this.barcolorIndex=-1
        },
       
    //   
    AssistTicket(){
        // goodId
        if(this.goodId==''){
            var self=this
            clearInterval(self.timer2);
            this.promptContent=''
            this.showPrompt=true
            self.timer2=setTimeout(function(){
                self.showPrompt=false
                clearInterval(self.timer2);
            },1000)
            return false;
        }else{ //
                if(this.addressIdIsSel=='false'){   // addressIdIsSelfalse 
                    var obj=qs.stringify({
                    })
                    this.$http.post("api/user/address_list",obj,{
                        headers: {
                                'authorization': this.tokenH
                            }
                    }).then((res)=>{
                        this.playDetailVoteDivs('true')  //true 
                        this.playDetailShopDESs({goodsId:this.goodId,playerId:this.playerId,barcolorIndex:this.barcolorIndex})  //
                        if(res.data.data.length){  //
                            this.ReceiptAddressPages('/PlayerDetails')
                            this.$router.push('/ReceiptAddress')
                        }else{  //
                            this.ReceiptAddressAddPages('/PlayerDetails')
                            this.$router.push('/ReceiptAddressAdd')
                        }
                    })
                }else if(this.addressIdIsSel=='true'){  //
                        this.AssistTicketTwo()
                }
        }
    },
    // 
    AssistTicketTwo(){
          var obj=qs.stringify({
              goods_id:this.goodId,
              player_id:this.playerId,
              address_id:this.addressId
          })
          this.$http.post('api/goods/goods_buyer',obj,{
                headers: {
                        'authorization': this.tokenH
                    }
            }).then((res)=>{
              if(res.data.code==200){
                  this.orderSn=res.data.data.result  //
                  this.getPayPermit()
              }else{
                   var self=this
                    clearInterval(self.timer2);
                    this.promptContent=res.data.msg
                    this.showPrompt=true
                    self.timer2=setTimeout(function(){
                        self.showPrompt=false
                        clearInterval(self.timer2);
                    },2000)
                    return false;
              }
          })
    },
     //    wx.config
        getPayPermit(){
            var PayPermitObj=qs.stringify({
                order_sn:this.orderSn,
                trade_type:'JSAPI',
            })
            this.$http.post('api/wechat/dopay',PayPermitObj,{
                    headers: {
                            'authorization': this.tokenH
                        }
                }).then((res)=>{
                    if(res.data.code==200){
                        var data=res.data.data
                        this.dataResult=data //
                        var vm=this
                        wx.config({
                            debug: false, // ,apialertpclogpc
                            appId: data.appId, // 
                            timestamp:data.timeStamp, // 
                            nonceStr: data.nonceStr, // 
                            signature: data.sign,// 
                            jsApiList: ['chooseWXPay'] // JS
                        });
                        this.payLe()
                    }else{
                        var self=this
                        clearInterval(self.timer2);
                                this.promptContent=res.data.msg
                                this.showPrompt=true
                                self.timer2=setTimeout(function(){
                                    self.showPrompt=false
                                    clearInterval(self.timer2);
                                },2000)
                            return false;
                    }
            })
        },
        //  wx.chooseWXPay
        payLe(){
                this.addressIdIsSels('false') //
                var vm=this
                wx.ready(function(){
                    wx.chooseWXPay({
                        timestamp: vm.dataResult.timeStamp, // jssdktimestamptimeStampS
                        nonceStr: vm.dataResult.nonceStr, //  32 
                        package: vm.dataResult.packag, // prepay_idprepay_id=\*\*\*
                        signType: 'MD5', // 'SHA1''MD5'
                        paySign:  vm.dataResult.sign, // 
                        success: function (res) {
                            this.myOrderListPages('/PlayerDetails')  //
                            vm.orderTypes('WAITSEND')  //orderType
                            vm.orderNums(2)   //
                            vm.$router.push('/orderList')  //
                        },
                        fail(){
                            
                            alert('')
                        }
                    });
                }) 
        },
    // goodid
      getShopDes(goodId,index){
          this.barcolorIndex=index  //
            this.goodId=goodId   
      },
    //   
      toReturn(){
          this.$router.push(this.PlayerDetailPage)  //PlayerDetailPagevuex
      },
    //   
      vote(){
          this.addressIdIsSels('false')   //  false,
          this.voteShow=true   //
      },
    //   
    freeticket(){
        var obj=qs.stringify({
            player_id:this.playerId
        })
        this.$http.post('api/user/spend_vote',obj,{
           headers: {
                'authorization':this.tokenH
            }
      }).then((res)=>{
          if(res.data.code==200){
               var self=this
            clearInterval(self.timer2);
                    this.promptContent=''
                    this.showPrompt=true
                    self.timer2=setTimeout(function(){
                        self.showPrompt=false
                        clearInterval(self.timer2);
                    },2000)
                return false;
          }else{
                    var self=this
                    clearInterval(self.timer2);
                            this.promptContent=res.data.msg
                            this.showPrompt=true
                            self.timer2=setTimeout(function(){
                                self.showPrompt=false
                                clearInterval(self.timer2);
                            },2000)
                        return false;
          }
      })
    },
    
    ...mapMutations(['playDetailVoteDivs','ReceiptAddressPages','ReceiptAddressAddPages','orderTypes','orderNums','playDetailShopDESs','addressIdIsSels','myOrderListPages','playerIds','shopgoodIds','barcolorIndexShops','shopDetatilshows','shopDetailReturns','userIdHs']),
  }
}

</script>
<style scoped lang="stylus">
// header  
.PlayerRanking_header{
    width:100%
    height:1.23rem;
    display :flex;
    align-items :center;
    justify-content :space-between;
    position :fixed;
    top:0;
    left:0;
    padding: 0 0.4rem;
    >img{
        width:0.53rem;
        height:0.45rem;
    }
    >span{
        font-size:0.427rem;
        color:#fff;
    }
}
.playerBigImg{
    width:100%;
    height:8.533rem;
    display :flex;
    >img{
        width:100%;
        height:6.213rem;
        z-index:-1;
    }
}
// 
.player_des{
    width:9.2rem;
    display :flex;
    flex-direction :column;
    background :#fff;
    box-shadow :0 0rem 0.3rem rgba(254, 208, 215, 0.5);
    border-radius:0.4rem 0.4rem 0 0rem;
    margin:0 auto;
    margin-top:-3.73rem;
    padding:0 0.4rem;
    .player_des_top{
        display :flex;
        justify-content :space-between;
        >.player_desImg{
            width:1.87rem;
            height:1.87rem;
            background :blue;
            border-radius:50%;
            margin-top:-0.53rem;
            border:0.1rem solid rgba(255, 255, 255, 1);
        }
        >ul{
            margin-top:0.333rem;
            >li{
                font-size:0.347rem;
                color:rgba(0, 0, 0, 1);
                line-height :0.747rem;
                text-align:center;
                border-radius:0.08rem;
                display:flex;
                align-items:center;
                justify-content :flex-end;
                >img{
                    width:0.293rem;
                    height:0.293rem;
                    margin-right:0.347rem;
                }
                >.username{
                    font-size:0.53rem;
                    color:rgba(51, 51, 51, 1);
                    line-height:0.747rem;
                }
                >.playerId{
                    margin-top:0.16rem;
                    line-height:0.493rem;
                    color:rgba(0, 0, 0, 1);
                }
            }
        }
    }
    >.piao_list{
        width:100%;
        display :flex;
        align-items :center;
        margin-bottom:0.4rem;
        position:relative;
        padding-left:0.25rem;
        >span{
            position:absolute;
            top:0.24rem;
            right:0;
            font-size:0.32rem;
            color:rgba(0, 0, 0, 0.36);
        }
        >li{
            display :flex;
            flex-direction :column;
            align-items :center;
            margin-right:1.07rem;
            >.piao_list_top{
                font-size:0.55rem;
                color:rgba(51, 51, 51, 1);
                line-height :0.727rem;
                margin-bottom:0.08rem;
            }
            >.piao_list_bottom{
                font-size:0.32rem;
                color:rgba(0, 0, 0, 0.36);
                line-height:0.44rem;
            }
        }
    }
    >.palylike{
        line-height:0.53rem;
        font-size:0.373rem;
        color:rgba(0, 0, 0, 1);
    }
    >.player_desName{
        font-size:0.53rem;
        color:rgba(0, 0, 0, 1);
        margin-bottom:0.2rem;
        >span{
            font-size:0.32rem;
            color:rgba(0, 0, 0, 0.36);
            margin-left:0.32rem;
            margin-top:0.32rem;
        }
    }
    
    .player_listdes{
        display:flex;
        margin-top:0.32rem;
        margin-bottom:0.4rem;
        >li{
            font-size:0.32rem;
            color:rgba(0, 0, 0, 0.36);
            margin-right:0.32rem;
            &:last-child{
                margin-right:0;
            }
            >img{
                width:0.27rem;
                height:0.27rem;
            }
        }
    }
    .player_btnList{
        display:flex;
        justify-content :space-between;
        margin-bottom:0.53rem;
        letter-spacing:0.04rem;
        >span{
            padding:0.2rem 0.25rem;
            font-size:0.427rem;
            border-radius:0.5rem;
        }
        >.voteBtn{
            background:rgba(255, 157, 172, 0.2);
            color:rgba(255, 157, 172, 1);
        }
        >.attentionBtn{
            background:rgba(74, 171, 249, 0.2);
            color:rgba(74, 171, 249, 1);
        }
        >.shareBtn{
            background:rgba(92, 216, 192, 0.2);
            color:rgba(92, 216, 192, 1);
        }
        >.EnterBtn{
            background:rgba(254, 217, 67, 0.2);
            color:rgba(254, 217, 67, 1);
        }
    }
    >.playNum{
        font-size:0.32rem;
        color:rgba(0, 0, 0, 1);
        margin-bottom:0.35rem;
    }
    
    >.player_desBtn{
        width:1.87rem;
        height:0.747rem;
        border-radius:0.67rem;
        background :rgba(75, 170, 249, 1);
        font-size:0.373rem;
        color:#fff;
        text-align :center;
        line-height :0.747rem;
        margin-bottom:0.427rem;
    }
    >.player_des_qu{
        font-size:0.32rem;
        color:rgba(0, 0, 0, 0.4)
    }
   
}
.playerImg_list{
    margin-top:0.32rem;
    display :flex;
    justify-content :space-between;
    flex-wrap:wrap;
    padding:0 0.4rem;
    >li{
        width:4.32rem;
        height:4.59rem;
        margin-bottom:0.4rem;
        background :skyblue;
        >img{
            width:100%;
            height:100%;
        }
    }

}
.vote_wrap{
    width:100%;
    height:100%;
    position :fixed;
    top:0;
    left:0;
}
.vote{
    width:100%;
    height:13.52rem;
    background:rgba(0, 0, 0, 0.7);
    position :fixed;
    bottom:0;
    left:0;
    // 
    >.AssistTicket{
        display:flex;
        flex-direction :column;
        align-items :center;
        padding:0 0.53rem;
        >.AssistTicket_title{
            width:100%;
            color:rgba(255, 255, 255, 1);
            font-size:0.427rem;
            line-height :0.613rem;
            margin-bottom:0.213rem;
        }
        >.AssistTicket_text{
            width:100%;
            font-size:0.32rem;
            color:rgba(196, 207, 232, 1);
            line-height :0.453rem;
        }
        >.SpecialTopicBody_bar{
            width:100%;
            margin-top:0.48rem;
            >ul{
                // height:4.533rem;
                display :flex;
                justify-content:space-between;
                flex-wrap:wrap;
                // align-items :center;
                // overflow-x:scroll;
                // display: -webkit-box;
                // -webkit-box-align: center;
                // align-items :center;
                z-index :-1;
                >li{
                    display:flex;
                    flex-direction:column;
                    margin-bottom:0.32rem;
                    margin-right:0.32rem;
                    >img{
                        width:2.6rem;
                        height:3.2rem;
                    }
                    >span{
                        margin-top:0.27rem;
                        color:rgba(255, 255, 255, 0.8);
                        font-size:0.32rem;
                    }
                }
                >.SpecialTopicBody_barColor{
                    border:0.07rem solid #FF5E4D;
                }
            }
        }
        >.AssistTicket_btn{
            width:3.253rem;
            height:0.853rem;
            background :rgba(255, 157, 172, 1);
            font-size:0.32rem;
            color:rgba(255, 255, 255, 1);
            border-radius:2.67rem;
            text-align :center;
            line-height :0.853rem;
            margin-top:0.48rem;
            margin-bottom:0.48rem;
        }
    }
    //
    >.FreeTicket{
        display :flex;
        justify-content:space-between;
        align-items :center;
        margin:0.53rem 0.4rem;
        >.FreeTicket_title{
            font-size:0.427rem;
            color:rgba(255, 255, 255, 1);
            line-height :0.613rem;
        }
        >.FreeTicket_btn{
            width:2.4rem;
            height:0.64rem;
            background:rgba(255, 157, 172, 1);
            font-size:0.347rem;
            color:rgba(255, 255, 255, 1);
            border-radius:2.67rem;
            text-align :center;
            line-height :0.64rem;
        }
    }
}
// 
.BigplayerImg_list{
    width:100%;
    height:100%;
    position:fixed;
    top:0;
    left:0;
    background:rgba(0,0,0,0.8)
    >li{
         width:100%;
        height:100%;
        display:flex;
        align-items:center;
        justify-content:center;
        >img{
            width:100%;
            height:auto;
        }
    }

}
// 
.promptFather{
    width:100%;
    position :absolute;
    bottom:50%;
    left:0;
    z-index:10;
    display :flex;
    justify-content :center;
    align-items :center;
    >.prompt{
        padding:0.15rem 0.3rem;;
        background :rgba(0,0,0,0.7);
        color:#fff;
        border-radius:0.5rem;
        font-size:0.32rem;
    }
}
.fade-enter-active, .fade-leave-active {
//   transition: opacity .5s;
    transition: all .3s ease;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
   transform: translateY(0.32rem);
  opacity: 0;
}
// 
    .small_Carousel{
        width:100%;
        height:1.2rem;
        position :absolute;
        top:1.4rem;
        left:0;
        background :rgba(0, 0, 0, 0.5)
        z-index:777;
        >.small_swiperWrap{
            width:100%;
            height:1.173rem;
             .swiperSlide{
                 display :flex;
                 align-items :center;
                 padding: 0 0.32rem;
                 font-size:0.373rem;
                 color:rgba(255, 255, 255, 1);
                 >img{
                     width:0.64rem;
                     height:0.64rem;
                     border-radius:50%;
                     margin-right:0.16rem;
                 }
                 >span{
                     overflow :hidden;
                     text-overflow :ellipsis;
                     white-space:nowrap;
                     font-size:0.32rem;
                     line-height:0.453rem;
                 }
            }
        }
       
    }
// 
</style>


